/* This example requires Tailwind CSS v2.0+ */
import { Fragment, Component } from "react";
import { Popover, Transition } from "@headlessui/react";
import {
  BookmarkAltIcon,
  CalendarIcon,
  ChartBarIcon,
  CursorClickIcon,
  MenuIcon,
  PhoneIcon,
  PlayIcon,
  RefreshIcon,
  ShieldCheckIcon,
  SupportIcon,
  ViewGridIcon,
  XIcon,
} from "@heroicons/react/outline";

class TailwindNavbar extends Component {
  render() {
    return (
      <Popover className="relative topbar">
        {({ open }) => (
          <>
            <div className="max-w-7xl mx-auto px-4 sm:px-6">
              <div className="flex justify-between items-center border-b-2 border-gray-100 py-6 md:justify-start md:space-x-10">
                {/* Left Logo */}
                {/*         <div className="flex justify-start lg:w-0 lg:flex-1">
                  <a href="#">
                    <span className="sr-only">Workflow</span>
                    <img
                      className="h-8 w-auto sm:h-10"
                      src="https://tailwindui.com/img/logos/workflow-mark-indigo-600.svg"
                      alt=""
                    />
                  </a>
                </div> */}
                {/* Left Logo End*/}

                {/* Open Menu Button */}
                <div className="-mr-2 -my-2 md:hidden">
                  <Popover.Button className=" rounded-md p-2 inline-flex items-center justify-center text-gray-400 hover:text-gray-500 hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-inset focus:ring-indigo-500">
                    <span className="sr-only">Open menu</span>
                    <MenuIcon className="h-6 w-6" aria-hidden="true" />
                  </Popover.Button>
                </div>
                {/* Open Menu Button End */}

                <div className="hidden md:flex items-center justify-around md:flex-1 lg:w-0">
                  <a
                    href="#"
                    className="whitespace-nowrap text-base font-medium text-gray-500 hover:text-gray-900"
                    onClick={this.onClickAbout}
                  >
                    About
                  </a>
                  <a
                    href="#"
                    className="whitespace-nowrap text-base font-medium text-gray-500 hover:text-gray-900"
                    onClick={this.onClickPortfolio}
                  >
                    Portfolio
                  </a>
                  <a
                    href="#"
                    className="whitespace-nowrap text-base font-medium text-gray-500 hover:text-gray-900"
                    onClick={this.onClickContact}
                  >
                    Contact
                  </a>
                </div>
              </div>
            </div>

            {/* Mobile Menu */}
            <Transition
              show={open}
              as={Fragment}
              enter="duration-200 ease-out"
              enterFrom="opacity-0 scale-95"
              enterTo="opacity-100 scale-100"
              leave="duration-100 ease-in"
              leaveFrom="opacity-100 scale-100"
              leaveTo="opacity-0 scale-95"
            >
              <Popover.Panel
                focus
                static
                className="absolute top-0 inset-x-0 p-2 transition transform origin-top-right md:hidden"
              >
                <div className="rounded-lg shadow-lg ring-1 ring-black ring-opacity-5 bg-white divide-y-2 divide-gray-50">
                  <div className="pt-5 pb-6 px-5">
                    <div className="flex items-center justify-between">
                      <div className="-mr-2">
                        <Popover.Button className="bg-white rounded-md p-2 inline-flex items-center justify-center text-gray-400 hover:text-gray-500 hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-inset focus:ring-indigo-500">
                          <span className="sr-only">Close menu</span>
                          <XIcon className="h-6 w-6" aria-hidden="true" />
                        </Popover.Button>
                      </div>
                    </div>
                    <div className="mt-6">
                      <nav className="grid gap-y-8">
                        <a
                          href="#"
                          className="-m-3 p-3 flex items-center rounded-md hover:bg-gray-50"
                          onClick={this.onClickAbout}
                        >
                          <span className="ml-3 text-base font-medium text-gray-900">
                            About
                          </span>
                        </a>

                        <a
                          href="#"
                          className="-m-3 p-3 flex items-center rounded-md hover:bg-gray-50"
                          onClick={this.onClickPortfolio}
                        >
                          <span className="ml-3 text-base font-medium text-gray-900">
                            Portfolio
                          </span>
                        </a>

                        <a
                          href="#"
                          className="-m-3 p-3 flex items-center rounded-md hover:bg-gray-50"
                          onClick={this.onClickContact}
                        >
                          <span className="ml-3 text-base font-medium text-gray-900">
                            Contact
                          </span>
                        </a>
                      </nav>
                    </div>
                  </div>
                </div>
              </Popover.Panel>
            </Transition>
          </>
        )}
      </Popover>
    );
  }

  onClickContact = (e) => {
    e.preventDefault();
    this.props.contactRef.current.scrollIntoView({
      behavior: "smooth",
      block: "start",
    });
  };
  onClickPortfolio = (e) => {
    e.preventDefault();
    this.props.portfolioRef.current.scrollIntoView({
      behavior: "smooth",
      block: "start",
    });
  };
  onClickAbout = (e) => {
    e.preventDefault();
    this.props.aboutRef.current.scrollIntoView({
      behavior: "smooth",
      block: "start",
    });
  };
}

export default TailwindNavbar;
